<template>
	<el-container>
		<el-header>
			<el-backtop @click.prevent.self="topa" :bottom="100" :visibility-height=1 disabled>
				<i class="el-icon-view"></i>
			</el-backtop>
			<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
				<el-menu-item index="1"><i class="el-icon-switch-button"></i></el-menu-item>
				<el-menu-item index="2"><i class="el-icon-info"></i></el-menu-item>
			</el-menu>
		</el-header>
		<el-main>
			<el-col :span="4">
				<!--  -->
				<el-card style="height: 650px;">
					<!--  -->
					<el-col :span="24">
						<el-tag style="width: 100%;background-color: white;"><i class="el-icon-time"></i>&nbsp;&nbsp;{{totalTimeText}}</span></el-tag>
					</el-col>
					<!--  -->
					<el-col :span="24">
						<el-tag type="success" style="width: 100%;"><i class="el-icon-view"></i>&nbsp;&nbsp;<br />
						</el-tag>
					</el-col>
					<!--  -->
					<el-col :span="24" v-show="leaveTime!=0">
						<el-tag type="warning" style="width: 100%;"><span><i class="el-icon-camera-solid"></i>&nbsp;&nbsp;{{leaveTime}}</span>
						</el-tag>
					</el-col>
					<!--  -->
					<el-col :span="24" style="border-radius: 30px">
						<video id="player" style="width: 100px;height: 100px;"></video>
						<canvas id="canvas" style="display:none;"></canvas>
						<el-button @click="stopCa()"></el-button>
					</el-col>
				</el-card>
			</el-col>
			<el-col :span="19" :offset="1">
				<!--  -->
				<el-card v-show="!isBegin" shadow="always" style="height: 650px;">
					<div @click.stop>
						<el-button @click="begin"></el-button>
					</div>
				</el-card>
				<!--  -->
				<el-card v-show="isBegin" shadow="always" style="height: 650px;">
					<span>

						

						

						https://wWw.ZuoWenwang.Net/

						

						

						</span>
				</el-card>
			</el-col>
		</el-main>
	</el-container>

</template>

<script>
	function formatTimeText(totalTime) {
		var h = Math.floor(totalTime / 60 / 60)
		var m = Math.floor((totalTime - (h * 60 * 60)) / 60)
		var s = (totalTime - (h * 60 * 60) - (m * 60))
		if (h < 10) {
			h = "0" + h
		}
		if (m < 10) {
			m = "0" + m
		}
		if (s < 10) {
			s = "0" + s
		}
		return h + " " + m + " " + s + ""
	}
	export default {
		name: 'Core',
		props: {},
		created() {

		},
		mounted() {
			this.$axios.defaults.withCredentials = true
			if (this.isCopy) {
				//
				this.$nextTick(() => {
					// 
					document.oncontextmenu = new Function("event.returnValue=false");
					// 
					document.onselectstart = new Function("event.returnValue=false");
				});
			}
			if (this.isLeave) {
				//
				window.addEventListener("focus", this.enter);
				window.addEventListener("blur", this.leave);
			}
		},
		data() {
			return {
				paperId: "123", //
				examineeId: "123", //id
				runningId: this.getUUID(), //

				isLimit: false, //
				isCopy: false, //
				isPaste: false, //
				isLeave: true, //
				isCamera: true, //
				isBegin: false, //
				totalTime: 120, //
				totalTimeText: '',

				activeIndex: '0', //
				leaveNum: 3, //
				leaveTime: 0, //

				detectionTime: 120, //

				timeId: "", //id

				player: null, //
			}
		},
		methods: {
			topa(event) {
				console.log(event)
			},
			enter() {
				console.log("")
			},
			leave() {
				if (this.isBegin) {
					this.leaveTime++
				}
			},
			handleSelect() {

			},
			begin() { //
				if (!this.isBegin) {
					if (this.isCamera) {
						this.vedioHandle_1() //
					}
					this.isBegin = true
					this.timeId = setInterval(this.timer_, 1000)
				}
			},
			vedioHandle_1() { //
				//1.
				this.$confirm(',, ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'info',
					center: true
				}).then(v => {
					this.vedioHandle_2()
				}).catch(() => {
					this.$message({
						type: 'error',
						message: ','
					});
				});
			},
			vedioHandle_2() { //
				this.$message({
					type: 'success',
					message: ',1',
					duration: 6000,
					iconClass: 'el-icon-camera-solid'
				});
				//2.
				this.cameraOpenHandle()
				//3. 
				//vue_thisthis
				let _this = this
				setTimeout(function() {
					_this.faceDetection()
				}, 3000)
				//4. 
				this.timeId = setInterval(this.timer__, 1000)
			},
			faceDetection() { //
				//
				let imgStr = this.getImage()
				imgStr = imgStr.replace("data:image/png;base64,", "")
				this.$axios({
					url: this.baseUrl + "/faceDetectionOp",
					method: "POST",
					params: {
						imgStr: imgStr,
						paperId: this.paperId,
						examineeId: this.examineeId,
						runningId: this.runningId,
					}
				}).then(res => {
					if (res.data.E_BACKSTATUS == '0') {
						this.$message({
							type: 'success',
							message: ',',
							duration: 1000,
						});
						//
						clearInterval(this.timeId)
						//
						this.vedioHandle_3()
					} else {
						this.$message({
							type: 'success',
							message: ',2',
							duration: 2000,
							iconClass: 'el-icon-camera-solid'
						});
						this.$message({
							type: 'error',
							message: res.data.E_BACKINFO,
							duration: 1000,
						});
						// 
						if (this.detectionTime > 0) {
							// 3000
							let _this = this
							setTimeout(function() {
								_this.faceDetection()
							}, 3000)
						}
					}
				})
			},
			vedioHandle_3() { //
				if (this.totalTime == 0) {
					return
				}
				//
				let imgStr = this.getImage()
				imgStr = imgStr.replace("data:image/png;base64,", "")
				this.$axios({
					url: this.baseUrl + "/faceCompareOp",
					method: "POST",
					params: {
						imgStr: imgStr,
						paperId: this.paperId,
						examineeId: this.examineeId,
						runningId: this.runningId,
					}
				}).then(res => {
					if (res.data.E_BACKSTATUS == '0') {
						//  
					} else {
						// 
						this.$message({
							type: 'error',
							message: ',',
							duration: 1000,
							iconClass: 'el-icon-camera-solid'
						});
					}
					if (this.totalTime > 0) {
						//  
						let nextTime = Math.floor(Math.random() * (12 - 1 + 1) + 1)
						console.log("" + nextTime + "")
						let _this = this
						setTimeout(function() {
							_this.vedioHandle_3()
						}, nextTime * 1000)
					}
				})
			},
			cameraOpenHandle() { //
				//
				if (navigator.mediaDevices === undefined) {
					navigator.mediaDevices = {};
				}
				if (navigator.mediaDevices.getUserMedia === undefined) {
					navigator.mediaDevices.getUserMedia = function(constraints) {
						var getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia;
						if (!getUserMedia) {
							return Promise.reject(new Error('getUserMedia is not implemented in this browser'))
							console.log("")
						}
						return new Promise(function(resolve, reject) {
							getUserMedia.call(navigator, constraints, resolve, reject);
						});
					}
				}
				const constraints = {
					video: true,
					audio: false
				};
				let videoPlaying = false;
				let v = document.getElementById('player');
				let promise = navigator.mediaDevices.getUserMedia(constraints);
				promise.then(stream => {
					if ("srcObject" in v) {
						v.srcObject = stream;
					} else {
						v.src = window.URL.createObjectURL(stream);
					}
					v.onloadedmetadata = function(e) {
						v.play();
						videoPlaying = true;
					};
				}).catch(err => {
					console.error(err.name + ": " + err.message);
				})
				this.player = v
			},
			getImage() { //
				let canvas = document.getElementById("canvas")
				canvas.getContext('2d').drawImage(this.player, 0, 0, 100, 100)
				// base64
				var image = canvas.toDataURL('image/png')
				return image
			},
			stopCa() { //
				this.player.srcObject.getTracks()[0].stop()
			},
			timer_() { //
				this.totalTime--
				this.totalTimeText = formatTimeText(this.totalTime)
				if (this.totalTime == 0) {
					this.isBegin = false
					this.totalTimeText = ""
					this.stopCa() //
					clearInterval(this.timeId)
				}
			},
			timer__() { //
				this.detectionTime--
				if (this.detectionTime == 0) {
					clearInterval(this.timeId)
					this.$message({
						type: 'error',
						message: ',',
						duration: 3000,
						iconClass: 'el-icon-camera-solid'
					});
				}
			}
		}
	}
</script>

<style>
</style>
