<template>
	<el-container>
		<el-header style="height: 0px;"></el-header>
		<el-row>
			<el-col :span="24">
				<el-steps :active="active" finish-status="success" simple>
					<el-step title=""></el-step>
					<el-step title=""></el-step>
					<el-step title=""></el-step>
					<el-step title=""></el-step>
				</el-steps>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="14" :offset="4">
				<el-divider></el-divider>
				<!--  -->
				<el-form v-show="active==0" ref="form" :model="setting" label-width="100px" label-position="left">
					<el-form-item label="">
						<el-input placeholder=",50" v-model="setting.title" clearable>
						</el-input>
					</el-form-item>
					<el-form-item label="">
						<el-col :span="11">
							<el-date-picker type="datetime" placeholder="" v-model="setting.date1" style="width: 100%;"></el-date-picker>
						</el-col>
						<el-col class="line" :span="2">-</el-col>
						<el-col :span="11">
							<el-date-picker type="datetime" placeholder="" v-model="setting.date2" style="width: 100%;"></el-date-picker>
						</el-col>
					</el-form-item>
					<el-form-item label="">
						<el-switch v-model="setting.delivery" style="margin-right: 92%;"></el-switch>
					</el-form-item>
					<el-form-item label="" v-show="setting.delivery">
						<el-autocomplete clearable popper-class="my-autocomplete" v-model="setting.duration" :fetch-suggestions="querySearch"
						 placeholder="" style="margin-right: 60%;">
							<template slot-scope="{ item }">
								<div class="name">{{ item.value }}</div>
							</template>
						</el-autocomplete>
					</el-form-item>
					<el-form-item label="">
						<el-input type="textarea" placeholder=",200" rows=5 v-model="setting.remark" maxlength="200"
						 show-word-limit>
						</el-input>
					</el-form-item>
				</el-form>
				<!--  -->
				<el-form v-show="active==1" ref="form" :model="invigilate" label-width="120px" label-position="left">
					<el-form-item label="">
						<el-col :span="8">
								<el-input-number v-model="invigilate.page" :min="1" :max="10"></el-input-number>
							</el-input>
						</el-col>
						<el-col class="line" :span="4"></el-col>
						<el-col :span="8">
							<el-input-number v-model="invigilate.num" :min="1" :max="10"></el-input-number>
						</el-col>
					</el-form-item>
					<el-form-item label="">
						<el-col :span="8">
							<el-switch v-model="invigilate.isCopy" style="margin-right: 75%;"></el-switch>
						</el-col>
						<el-col class="line" :span="4"></el-col>
						<el-col :span="8">
							<el-switch v-model="invigilate.isPaste" style="margin-right: 75%;"></el-switch>
						</el-col>
					</el-form-item>
					<el-form-item label="">
						<el-col :span="8">
							<el-switch v-model="invigilate.isCamera" style="margin-right: 75%;"></el-switch>
						</el-col>
					</el-form-item>
				</el-form>
				<!--  -->
				<el-form v-show="active==2" ref="form" :model="invigilate" label-position="left">
					<el-form-item>
						<el-row>
							<el-button type="primary" plain><i class="el-icon-zoom-in"></i>&nbsp;</el-button>
							<el-button type="success" plain><i class="el-icon-circle-plus"></i>&nbsp;</el-button>
							<el-button type="info" plain><i class="el-icon-upload2"></i>&nbsp;</el-button>
						</el-row>
					</el-form-item>
				</el-form>
				<!--  -->
				<el-form v-show="active==3" ref="form" :model="invigilate" label-position="left">
					<el-form-item>
						<el-row>
							<el-button type="primary" plain><i class="el-icon-s-promotion"></i>&nbsp;</el-button>
							<el-button type="success" plain><i class="el-icon-s-order"></i>&nbsp;</el-button>
						</el-row>
					</el-form-item>
				</el-form>
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="21">
				<el-button v-show="active>0" style="margin-top: 12px;" @click="toStep(-1)"></el-button>
				<el-button v-show="active<3" style="margin-top: 12px;" @click="toStep(1)"></el-button>
			</el-col>
		</el-row>
	</el-container>
</template>

<script>
	import Question from "./addQuestion.vue"
	export default {
		name: 'make',
		props: {
		},
		data() {
			return {
				active: 0,
				setting: { //
					title: '',
					region: '',
					date1: '',
					date2: '',
					duration: '30',
					delivery: false,
					type: [],
					resource: '',
					desc: '',
					remark: '',
				},
				invigilate: {
					page: 0,
					num: 1,
					isCopy: false,
					isPaste: false,
					isCamera:false
				},
				restaurants: [],
			};
		},
		mounted() {
			this.restaurants = this.loadAll();
		},
		methods: {
			querySearch(queryString, cb) {
				var restaurants = this.restaurants;
				var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
				//  callback 
				cb(results);
			},
			createFilter(queryString) {
				return (restaurant) => {
					return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
				};
			},

			loadAll() {
				return [{
						"value": "30",
					},
					{
						"value": "60",
					},
					{
						"value": "90",
					},
					{
						"value": "120",
					},
					{
						"value": "150",
					},
				];
			},
			handleSelect(item) {
				console.log(item);
			},
			handleIconClick(ev) {
				console.log(ev);
			},
			toStep(i) {
				if (this.active + i > 3 || this.active + i < 0) return
				this.active += i
			},
			onSubmit() {
				console.log('submit!');
			}
		},
	}
</script>

