<style type="text/css">
	.el-menu-vertical-demo:not(.el-menu--collapse) {
		width: 200px;
		min-height: 400px;
	}
</style>
<template>
	<el-container>
		<!--  -->
		<el-header>
			<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
				<el-menu-item index="100"></el-menu-item>
				<el-menu-item index="101">
					<el-avatar src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
					<span style="margin-left: 10px;margin-top: 5px;">Durry</span>
				</el-menu-item>
			</el-menu>
		</el-header>

		<el-container style="margin-top: 10px;">
			<!--   -->
			<el-aside v-show="asideMenu==100" width="220px">
				<el-menu class="el-menu-vertical-demo" @select="handleSelectAside" @open="handleOpen" @close="handleClose">
					<el-submenu index="">
						<template slot="title">
							<i class="el-icon-location"></i>
							<span></span>
						</template>
						<el-menu-item-group>
							<el-menu-item index="/Make"><i class="el-icon-document-add"></i>
							</el-menu-item>
							<el-menu-item index="/List"><i class="el-icon-setting"></i></el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-menu-item index="" @click="dialogVisible = true">
						<span slot="title"><i class="el-icon-menu"></i></span>
					</el-menu-item>
					<el-menu-item index="">
						<i class="el-icon-document"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-menu-item index="">
						<i class="el-icon-setting"></i>
						<span slot="title"></span>
					</el-menu-item>
				</el-menu>
			</el-aside>
			<!--   -->
			<el-aside v-show="asideMenu==101" width="220px">
				<el-menu class="el-menu-vertical-demo" @select="handleSelectAside" @open="handleOpen" @close="handleClose">
					<el-submenu index="">
						<template slot="title">
							<i class="el-icon-location"></i>
							<span></span>
						</template>
						<el-menu-item-group>
							<el-menu-item index="/Make"><i class="el-icon-document-add"></i>
							</el-menu-item>
							<el-menu-item index="/List"><i class="el-icon-setting"></i></el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-menu-item index="">
						<i class="el-icon-menu"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-menu-item index="" disabled>
						<i class="el-icon-document"></i>
						<span slot="title"></span>
					</el-menu-item>
					<el-menu-item index="">
						<i class="el-icon-setting"></i>
						<span slot="title"></span>
					</el-menu-item>
				</el-menu>
			</el-aside>
			<!--  -->
			<el-container style="height: 800px;">
				<el-main>
					<el-tabs v-model="activeTab" type="card" editable @tab-click="selectTab" @edit="handleTabsEdit" @tab-remove="handleTabsClose">
						<el-tab-pane :key="item.name" v-for="(item, index) in editableTabs" :label="item.title" :name="item.name">
							{{item.content}}
						</el-tab-pane>
					</el-tabs>
					<router-view></router-view>
				</el-main>
			</el-container>
		</el-container>
		<el-container>
			<!--  -->
			<el-footer>
				<div>
					<el-divider></el-divider>
					<span>2020-2021 Durry. All rights reserved.</span>
				</div>
				<el-dialog :visible.sync="dialogVisible" width="45%" @close="dialogVisible=false">
					<el-input placeholder="" v-model="testLink" clearable>
					</el-input>
				</el-dialog>
			</el-footer>
		</el-container>
	</el-container>
</template>

<script>
	let tabMap = new Map([
		['/Make', ''],
		['/Welcome', ''],
		['/List', ''],
	]) //router-view

	export default {
		name: 'home',
		props: {

		},
		components: {},
		data() {
			return {
				asideMenu: 100,
				testLink: "",
				dialogVisible: false,
				visible: false,
				loading: true,
				isCollapse: true,
				activeIndex: '100',
				isCollapse: true,
				fullscreenLoading: false,
				activeTab: '', //
				preTab: '', //
				editableTabs: [], //
				tabIndex: 1,
			};
		},
		mounted() {
			// var path = this.$route.path
			// console.log(":" + path)
			// if (path == '/') {
			// 	path = '/Welcome'
			// }
			// if (!tabMap.has(path)) {
			// 	path = '/Welcome'
			// }
			// this.handleSelectAside(path)
		},
		watch: {
			$route: {
				handler(val, oldval) {
					// console.log(oldval)
					// console.log(val); //
					// var path = val.path
					// if (!tabMap.has(path)||path == '/') {
					// 	path = '/Welcome'
					// }
					// console.log("watch",path)
					// this.handleSelectAside(path)
				},
				// 
				deep: true
			}
		},
		methods: {
			initPage() {
				
			},
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			handleSelect(key, keyPath) {
				this.asideMenu = key
			},
			//   
			handleSelectAside(key) {
				if (!tabMap.has(key)) {
					return
				}
				console.log(":" + key)
				//1.
				this.$router.push(key)
				console.log(this.hasTab(key))
				if (this.hasTab(key)) { //2.
					//2.1
					this.activeTab = key
					this.preTab = key
					return
				}
				//3. 
				this.editableTabs.push({
					title: tabMap.get(key),
					name: key,
					closable: true
				})

				console.log(":" + key)
				//4.
				this.activeTab = key
				this.preTab = key
			},
			//
			selectTab(data) {
				console.log(":" + this.activeTab)
				if (this.activeTab == this.preTab) {
					//2. 
					return
				}
				//3. 
				this.preTab = this.activeTab
				this.$router.push(this.activeTab)
			},
			handleTabsClose(data) {},
			handleTabsEdit(targetName, action) {
				if (action === 'add') {
					// let newTabName = ++this.tabIndex + '';
					// this.editableTabs.push({
					// 	title: 'New Tab',
					// 	name: newTabName,
					// 	content: 'New Tab content'
					// });
					// this.editableTabsValue = newTabName;
				}
				if (action === 'remove') {
					let tabs = this.editableTabs;
					console.log(":" + targetName)
					let welcomeName = "/Welcome"
					let activeName = this.activeTab;
					if (activeName === targetName) {
						console.log("acv")
						//1.  
						tabs.forEach((tab, index) => {
							if (tab.name === targetName) {
								//2.
								let nextTab = tabs[index + 1] || tabs[index - 1];
								if (nextTab) {
									//3.
									activeName = nextTab.name;
								} else {
									console.log("finally")
									//5.  
									activeName = welcomeName
								}
							}
						});
					}

					this.activeTab = activeName;
					this.preTab = activeName
					this.editableTabs = tabs.filter(tab => tab.name !== targetName);
					this.handleSelectAside(activeName)
				}
			},
			hasTab(key) {
				var flag = false
				this.editableTabs.forEach((tab, index) => {
					if (tab.name == key) {
						flag = true
					}
				});
				return flag
			}
		}
	}
</script>
